export default {
	data() {
		return {
			agentName: '',
			todayAmount: '',
			todayPeople: '',
			yesAmount: '',
		}
	},
	created() {
		// this.__init()
	},
	onLoad() {
		this.__init()
	},
	methods: {
		async __init() {
			this.$H.post("/agent/", {
				user_id: "183823",
				token: "dXQyMDIwMDMxODExMTgzNjY3MjAxNTU5",
				opt: "home"
			}).then((res) => {
				console.log(res);
				// let data = data
				this.agentName = res.user_info.agent_info.agent_name
				this.todayAmount = res.today.total_amount
				this.todayPeople = res.today.total_people
				this.yesAmount = res.yesterday.total_amount
			}).catch((error) => {
				console.log(error);
			})
			
		}
		
	}
}
